          departmentID: "", //部门（ID）
          projectId:'',  //项目（ID）
          approveStatus: "", //流程状态
          orderBy: "startDate desc", //排序
          curUser: "" //定义的审批人
        },
        selectForm2: {
          searchWord: "", //搜索
          status: "",//流程状态
          originator: "", //发起人
          amountOfMoneyB: "", //金额低
          amountOfMoneyE: "", //金额高
          startDate: "", //开始时间
          finishDate: "", //结束时间
          processFullName: "", //流程类别
          companyCode:'',//公司 （ID）
          departmentID: "", //部门（ID）
          projectId:'',  //项目（ID）
          approveStatus: "", //流程状态
          orderBy: "startDate desc", //排序
          curUser: "" //定义的审批人
        },
        startDate:[],
        delegation:[],
        finshDate:[],
      },
      showdialog: false, //弹窗初始显示状态
      showList: true, //动态table高度
      rules: {},
      tableData: [], // 定义接口初始化
      //流程的tag
      serachSlectData:{
        tagsOwnProcess:[],//tags带来的数据
        tagsOwnCategory:[],//流程类别
        tagsOrgSelect:[],
        CurrentUser:[],//当前处理人
        tagsOwnsUser:[]
      },
      selectOptions: [],
      list: [],
      loading: false, //loading加载
      pageSizesList: [10,20], //分页页码
      pageShow: false, //分页初始显示状态
      rowData: {}, //每行数据
      total: 0, //初始总数,
      showNone: false, //邮件弹窗显示隐藏
      showMess: false, //短信弹窗显示隐藏
      showremove: false, //刪除弹窗初始显示状态
      overremove: false, //結束弹窗初始显示状态
      Visible:false,  //弹窗显示
      imgLoading: false,
      // 流程类别名称
      processFormName: "",
      dataTitle: "",
      CriteriaModel:{
        page:1,
        limit:10,
        sType:'1',
        fromUser:'',//授权人
        toUser:'',//代理人
        processName:'',
        startTime:'',
        endTime:'',
        status:0,//初始状态是全部
      },//获取我的代办初始参数定义
      status:0,//初始状态是全部
      tagsOwnsUser:[],//选人员
      ProcessType:'',//流程类型，用于父组件给子组件
      typeTab:'User',
      portalmMtilselectCheck:'false',//单选多选配置
      echoData:[],
      switchText:'',
      isValid:false,//是否有效
      addComment:'',//备注
      readonly:false,//是否只读
      toUser:'',
      editFromUser:'',
      proxyTitle:'',
      clickRow:{
        flag:'',
        id:'',
      }
    };
  },
  mounted() {
    var that = this;
    util.burialPoint('流程查询-进入查询流程页面');
    that.$nextTick(()=>{
      that.getEmpower();
      that.getlist();
    });
  },
  methods: {
    // 点击input 不显示控件 点击icon图标显示控件
    hideDate(val) {
      util.burialPoint('查询流程-输入条件-发起时段');
      setTimeout(() => {
        let dateNodeStart = undefined;
        let dateNodeEnd = undefined;
        let startBool = null;
        let endBool = null;
        if (document.getElementsByClassName('el-picker-panel')[0]) {
          startBool = true;
        }
        if (document.getElementsByClassName('el-picker-panel')[1]) {
          endBool = true;
        }

        dateNodeStart = startBool ? document.getElementsByClassName('el-picker-panel')[0] : '';
        dateNodeEnd = endBool ? document.getElementsByClassName('el-picker-panel')[1] : '';

        if (val.$el.firstElementChild == document.activeElement) {
          startBool ? dateNodeStart.style.display = 'none' : '';
          endBool ? dateNodeEnd.style.display = 'none' : ''
        } else {
          startBool ? dateNodeStart.style.display = 'block' : '';
          endBool ? dateNodeEnd.style.display = 'block' : ''
        }
      }, 10)
    },
    // 输入框清空
    emptyselect() {
      this.childDataUrl = {};
      this.examineUser = [];
      this.model.managerName = [];
    },
    //表格排序
    sortChange(column) {
      util.burialPoint('查询流程-列表-' + column.column.label + '排序');
      let custom = column.prop == "statusName" ? "dodoType" : column.prop
      this.model.selectForm.orderBy = `${custom} ${column.order.replace("ending", "")}`;
      this.pageIndex = 1;
      this.getlist();
    },
    //设置表格列头
    renderIcon(create, {column}) {
      return create("i", {class: "iconfont icon-process"});
    },
    /**
     * 金额验证
     */
    checkValidate(position, value) {
      value = parseFloat(value);
      let that = this;
      if (position == "front") {
        if (
          this.model.selectForm.amountOfMoneyE != "" &&
          value > parseFloat(this.model.selectForm.amountOfMoneyE)
        ) {
          that.model.selectForm.amountOfMoneyB = "";
        }
      } else if (position == "back") {
        if (
          this.model.selectForm.amountOfMoneyB != "" &&
          value < parseFloat(this.model.selectForm.amountOfMoneyB)
        ) {
          that.model.selectForm.amountOfMoneyE = "";
        }
      }
    },
    //金额输入中验证
    inputValidate(position, value) {
      let that = this;
      let regPos = /^([0-9]*)([.]?|(\.\d{1,2})?)$/; //验证金额正则
      if (position == "front") {
        if (!regPos.test(value)) {
          setTimeout(function () {
            that.model.selectForm.amountOfMoneyB = "";
          }, 100);
        }
      } else if (position == "back") {
        if (!regPos.test(value)) {
          setTimeout(function () {
            that.model.selectForm.amountOfMoneyE = "";
          }, 100);
        }
      }
    },
    //重置数据
    refresh() {
    this.model.delegation = [];
    this.model.finshDate = [];
    this.toUser = '';
    this.status = 0;
    this.CriteriaModel.processName = '';
    this.serachSlectData.tagsOwnsUser = [];
    this.serachSlectData.tagsOwnProcess = [];
    },
    //审批人验证延时后的值是否跟当前文本框值相同
    validationInputed(managerName) {
      this.orgStatesed = [];
      if (managerName == [] || managerName.length == 0) {
        this.loading = false;
        return null;
      }
      if (managerName == this.$refs.userIds.query) {
        this.getUsers(managerName, "examine");
      } else {
        this.orgStatesed = [];
      }
    },
    //搜索
    search() {
      util.burialPoint('查询流程-输入条件-搜索按钮');
      this.pageIndex = 1;
      this.getlist();
    },
    //弹窗确定
    correctremovedel(){
      let list = [];
      let fromUserAccount = '';
      let toUserName = '';
      let toUserAccount = '';
      let startDate='';
      let endDate='';
      this.CriteriaModel.fromUser = localStorage.getItem('username');
      if(this.serachSlectData.tagsOwnsUser[0]){
        toUserName = this.serachSlectData.tagsOwnsUser[0].UserDisplayName
      }
      else {
        this.$message.warning('请选择代理人');
        return
      }
      if(this.serachSlectData.tagsOwnsUser[0]){
        toUserAccount = this.serachSlectData.tagsOwnsUser[0].UserAccount
      }
      if(this.model.startDate[0]){
        startDate=moment(this.model.startDate[0]).format("YYYY-MM-DD HH:mm:ss")
      }
      else {
        this.$message.warning('请选择开始时间');
        return
      }
      if(this.model.startDate[1]){
        endDate=moment(this.model.startDate[1]).format("YYYY-MM-DD HH:mm:ss")
      }
      else {
        this.$message.warning('请选择结束时间');
        return
      }
      if(this.serachSlectData.tagsOwnProcess.length>0){
        for(let item of this.serachSlectData.tagsOwnProcess){
          list.push({
            CategoryId:item.CategoryId,
            CategoryName:item.CategoryName,
            ProcessCode:item.ProcessCode,
            ProcessName:item.ProcessName,
          })
        }
        list = JSON.stringify(list);
      }
      else{
        this.$message.warning('请选择流程');
        return
      }
      if(this.clickRow.flag ==='edit'){
        let data = {
          Id: this.clickRow.id,
          sType:this.CriteriaModel.sType,
          fromUserName:this.CriteriaModel.fromUser,
          fromUserAccount:localStorage.getItem('UserAccount'),
          toUserName:toUserName,
          toUserAccount:toUserAccount,
          isEnabled:this.isValid,
          start:startDate,
          end:endDate,
          processList:list,
          txtNote:this.addComment
        };
        this.$axios({
          url:baseUrl.Base_Delegation_URL+'/UpdateProcessDelegation',
          method:'post',
          data:data,
        }).then(res=>{
          if(res.data.code===0){
            this.getlist();
            this.showremove = false;
          }
        }).catch(err=>{
          this.$message.error(`${err.message}`);
        });
      }
      else {
        let data = {
          sType:this.CriteriaModel.sType,
          fromUserName:this.CriteriaModel.fromUser,
          fromUserAccount:localStorage.getItem('UserAccount'),
          toUserName:toUserName,
          toUserAccount:toUserAccount,
          isEnabled:this.isValid,
          start:startDate,
          end:endDate,
          processList:list,
          txtNote:this.addComment
        };
        this.$axios({
          url:baseUrl.Base_Delegation_URL+'/AddProcessDelegation',
          method:'post',
          data:data,
        }).then(res=>{
          if(res.data.code===0){
            this.getlist();
            this.showremove = false;
          }
        }).catch(err=>{
          this.$message.error(`${err.message}`);
        });
      }
    },
    // 点击删除流程的取消按钮成功并提示
    cancelremovedel() {
      var that = this;
      that.showremove = false;
    },
    //获取列表
    getlist() {
      var that = this;
      switch (that.status){
        case 0:{
          that.CriteriaModel.status = 'All';
          break
        }
        case 1:{
          that.CriteriaModel.status = 'true';
          break
        }
        case 2:{
          that.CriteriaModel.status = 'false';
          break
        }
      }
      //开始时间
      if(that.model.delegation.length===0){
        that.CriteriaModel.startTime = '';
      }
      else {
        let startTimeArr = [];
        that.model.delegation[0] = moment(that.model.delegation[0]).format("YYYY-MM-DD HH:mm");
        that.model.delegation[1] = moment(that.model.delegation[1]).format("YYYY-MM-DD HH:mm");
        startTimeArr.push(that.model.delegation[0],that.model.delegation[1]);
        that.CriteriaModel.startTime = startTimeArr.join('-');
      }
      //结束时间
      if(that.model.finshDate.length===0){
        that.CriteriaModel.endTime = '';
      }
      else {
        let endTimeArr = [];
        that.model.finshDate[0] = moment(that.model.finshDate[0]).format("YYYY-MM-DD HH:mm");
        that.model.finshDate[1] = moment(that.model.finshDate[1]).format("YYYY-MM-DD HH:mm");
        endTimeArr.push(that.model.finshDate[0],that.model.finshDate[1]);
        that.CriteriaModel.endTime = endTimeArr.join('-');
      }
      //代理人
      that.CriteriaModel.toUser = that.toUser;
      that.imgLoading = true;
      that.$axios({
        url: baseUrl.Base_Delegation_URL+'/GetProcessDelegation',
        data: that.CriteriaModel,
        method: 'post'
      }).then(result => {
        if (result.status === 200) {
          // 已办数据集合
          that.tableData = result.data.data;
          // 数据总数
          that.total = result.data.count;
          for (let item of  that.tableData) {
            item.EndDate = item.EndDate ? moment(item.EndDate).format('YYYY-MM-DD HH:mm') : ''
          }
        }
        else {
          that.$message.error("请求失败！");
        }
        that.imgLoading = false;
      })
        .catch(error => {
          this.imgLoading = false;
          that.$message.error(`查询列表数据失败!ERROR MESSAGE:${error}`);
        });
    },
    //新增代理
    delegationAdd() {
      this.proxyTitle = '新增流程代理';
      this.readonly = false;
      //打开弹窗之前清空上一次选中的数据
      this.addComment = '';
      this.isValid = false;
      this.echoData = [];
      this.serachSlectData.tagsOwnProcess=[];
      this.serachSlectData.tagsOwnsUser=[];
      this.model.startDate = [];
      this.model.finshDate = [];
      this.showremove = true;
    },
    //状态切换
    changeSwitch(row){
      let that = this;
      let data = {
        Id:row.Id,
        Status:row.IsEnable
      };
      that.$axios({
        url: baseUrl.Base_Delegation_URL+'/SetDelegationStatus',
        data: data,
        method: 'post',
      }).then(result => {
          if (result.status === 200) {
            that.$message.success('设置成功');
          }
      }).catch(err=>{
        that.$message.error(`查询列表数据失败!ERROR MESSAGE:${err.message}`);
      });
      that.getlist();
    },
    //查看
    handleClickView(row){
      this.getlistItemById(row.Id)
      this.proxyTitle = '查看流程代理';
      this.showremove = true;
      this.readonly = true;
      this.editFromUser = row.ToUserName;
      this.model.startDate[0] = moment(row.StartDate).format('YYYY-MM-DD HH:mm');
      this.model.startDate[1] = row.EndDate;
      this.isValid = row.IsEnable;
      this.addComment = row.Note;
    },
    //删除流程
    handleDel(row){
      let index = this.serachSlectData.tagsOwnProcess.indexOf(row);
      this.serachSlectData.tagsOwnProcess.splice(index,1);
    },
    //编辑
    handleClickEdit(row){
      this.clickRow.flag = 'edit';
      this.clickRow.id = row.Id;
      this.getlistItemById(row.Id);
      this.proxyTitle = '编辑流程代理';
      this.showremove = true;
      this.readonly = false;
      if( this.serachSlectData.tagsOwnsUser.length===0){
        this.serachSlectData.tagsOwnsUser.push({
          UserDisplayName:row.ToUserName,
          type:'User'
        });
      }
      this.model.startDate[0] = moment(row.StartDate).format('YYYY-MM-DD HH:mm');
      this.model.startDate[1] = row.EndDate;
      this.isValid = row.IsEnable;
      this.addComment = row.Note;
    },
  /**
     * 页面size变更方法
     */
    handleSizeChange(val) {
      util.burialPoint('查询流程-列表-每页显示条数按钮');
      this.pageShow = true;
      var pageSize = `${val}`;
      this.CriteriaModel.PageIndex = 1;
      this.CriteriaModel.limit = parseInt(pageSize);
      localStorage.checkPageSize = val;
      this.getlist();
    },
    /**
     * 翻页方法
     */
    handleCurrentChange(val) {
      util.burialPoint('查询流程-列表-页码按钮');
      this.CriteriaModel.page = val;
      this.getlist();
      this.pageShow = true;

    },
    //设置表头标题class
    tableHeaderClassName({row, rowIndex}) {
      return "table-head-th";
    },

    choosenDialogType(val, type) {
      that.typeTab = val;
      //打开流程弹窗
      that.ProcessType = type;
      that.$nextTick(()=>{

        that.$refs.dialogChoosen.openChoosen(that.typeTab, type);
        //that.$refs.dialogChoosen.getCollect();
      })

    },
    //打开弹窗
    OpenDialog(val) {
      var that = this;
      that.Visible = val;
    },
    //关闭弹窗
    CloseDialog(v) {
      var that = this;
      that.Visible = v;
    },
    //接收子组件确定按钮带过的数据
    sendTag(val, type) {
      let _this = this;
      if (val.length > 0) {
        if (type) {
          for (let item of val) {
            if (type === 'User') {
              let processArr = _this.getSysId(_this.serachSlectData.tagsOwnsUser);
              let index = processArr.indexOf(item.SysId);
              if(index ===-1){
                _this.serachSlectData.tagsOwnsUser.push(item);
              }
            }
            //测试完全正常
            if (type === 'Process') {
              let processArr = _this.getID(_this.serachSlectData.tagsOwnProcess);
              let index = processArr.indexOf(item.id);
              if(index ===-1){
                _this.serachSlectData.tagsOwnProcess.push(item);
              }
            }
          }
        }
        else {
          for (let item of val) {
            if (item.type === 'User') {
              let processArr = _this.getSysId(_this.serachSlectData.tagsOwnsUser);
              let index = processArr.indexOf(item.SysId);
              if(index ===-1){
                _this.serachSlectData.tagsOwnsUser.push(item);
              }
            }
          }
        }
      }
      else {
        switch (type) {
          case 'Process': {
            _this.serachSlectData.tagsOwnProcess = [];
            break
          }
          case 'User': {
            _this.serachSlectData.tagsOwnsUser = [];
            break;
          }
          case 'CurrentUser': {
            _this.serachSlectData.CurrentUser = [];
            break;
          }
        }
      }
    },
    //删除页面回显中的tag
    closeProcessTag(item) {
      if (item.portalType) {
        if (item.portalType === 'CurrentUser') {
          let index = this.serachSlectData.CurrentUser.indexOf(item);
          this.serachSlectData.CurrentUser.splice(index, 1);
          this.$refs.dialogChoosen.portalehco(this.serachSlectData.CurrentUser, null);
        }
      }
      else {
        switch (item.type) {
          case 'Process': {
            let index = this.serachSlectData.tagsOwnProcess.indexOf(item);
            this.serachSlectData.tagsOwnProcess.splice(index, 1);
            this.$refs.dialogChoosen.portalehco(this.serachSlectData.tagsOwnProcess);
            break;
          }
          case 'User': {
            this.serachSlectData.tagsOwnsUser = [];
            this.$refs.dialogChoosen.portalehco(this.serachSlectData.tagsOwnsUser, null);
            break;
          }
        }
      }
    },
    //流程单号跳转
    folioBlank(row) {
      window.open(row.Url, '_blank');
    },
    //获取当前登录人
    getEmpower() {
      let that = this;
     if(localStorage.getItem('username')){
       that.CriteriaModel.fromUser = localStorage.getItem('username')
     }else {
       that.$axios({
         url: 'Home/GetCurrentUser',
         methods: 'get'
       }).then(result => {
         that.CriteriaModel.fromUser = result.data.data;
         if (!result.data.data) {
           that.CriteriaModel.fromUser = 'Admin'
         }
       }).catch(err => {
         that.$message.error(`获取用户信息数据失败!ERROR MESSAGE:${err.message}`);
       })
     }
    },
    handleClick(tab, event){
        this.getlist();
    },
    //获取所有唯一的id
    getID(arr){
      let list = [];
      if(arr){
        for(let item of arr){
          let id = item.id;
          if(id){
            list.push(id)
          }
        }
      }
      return list
    },
    getSysId(arr){
      let list = [];
      if(arr){
        for(let item of arr){
          let SysId = item.SysId;
          if(SysId){
            list.push(SysId)
          }
        }
      }
      return list
    },
    getlistItemById(id){
      let data = {
        page:1,
        limit:5,
        id:id
      };
        this.$axios({
          url:baseUrl.Base_Delegation_URL+'/GetDelegationProcessList',
          method:'post',
          data:data
        }).then(res=>{
          if(res.status===200){
            this.serachSlectData.tagsOwnProcess = res.data.data;
          }
        }).catch(err=>{

        })
    },
  },
  //计算方法
  watch: {
    "model.userName": {
      handler: function(n, o) {
        let ele = this.$refs.userId;
        util.inputHeight(n, ele);
        this.dataTitle = "";
        // 监听数组list 的第一项的name值变化；n 为新数据，o 为旧数据
        for (var i = 0; i < n.length; i++) {
          if (typeof n[i] == "object") {
            this.dataTitle += n[i].value + ",";
          } else if (typeof n[i] == "string") {
            this.dataTitle += n[i] + ",";
          }
        }
        this.dataTitle = this.dataTitle.substr(0, this.dataTitle.length - 1);
      },
      deep: true
    },
    "model.date":{
      handler: function(n, o) {
        this.startDate = false;
        this.$nextTick(() => {
          this.startDate = true
        });
        this.endDate = false;
        this.$nextTick(() => {
          this.endDate = true
        })
      },
      deep: true
    },
     Visible:{
      handler(n, o) {
        if(n){
          switch (this.typeTab){
            case 'User':{
              this.echoData = [...this.serachSlectData.tagsOwnsUser];
              break
            }
            case 'Org':{
              this.echoData = [...this.serachSlectData.tagsOrgSelect];
              break
            }
          }
          switch (this.ProcessType){
            case 'Process':{
              this.echoData = [...this.serachSlectData.tagsOwnProcess];
              break
            }
            case 'Category':{
              this.echoData = [...this.serachSlectData.tagsOwnCategory];
              break
            }
            case 'CurrentUser':{
              this.echoData = [...this.serachSlectData.CurrentUser];
              break
            }
            case 'ToWrite':{
              this.echoData = [...this.serachSlectData.ToWriteData];
              break
            }
          }
        }
      }
    },
  },
  computed: {
    bodyHeight() {
      return document.documentElement.clientHeight
    },
    ...mapGetters(["isIE", "isSafari", "bodyHeight"])
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  .list{
    a{
      cursor: pointer;
    }
    a:hover{
      text-decoration: underline;
      color: #00a1f1;
    }
  }
  .delegationAdd{
   margin: 10px 0;
    .el-button{
      padding: 8px 15px;
    }
  }
</style>



